import React from 'react'

//calling multiple components
//double curly brackets represent i)javascriptand ii)javascript object here 

//there are two ways to access props i) destructuring (javascript concept) ii)children
const Book = ({img,title,author}) => {
  // props is an object .if we console.log it 
  // const {img,title,author}= props;

  //attributes , eventhandler
  // onClick,onMouseOver
  const clickHandler= (e) => {
    console.log(e);
    console.log(e.target);
   alert ('hello World');
  };
   
  const complexExercise= (author) =>{
   console.log(author);
  };
  
    return (
    <article className="book" onMouseOver={() => {
      console.log(title);
    }}>
      <img src={img} alt='' />
      <h2>{title}</h2>
      <h4>{author}</h4>
      <button type="button" onClick={clickHandler}> Reference Example</button> 
      <button type="button" onClick={()=>complexExercise(author)}>another Example</button>
    </article>
  );
};


export default Book ;