import React from 'react';
import ReactDom from 'react-dom';

// CSS 
import './index.css';
//.js extension is not needed here unlike .css extension above 
import {data} from './books'
import Book from './Book'
//must return a JSX
//JSX rules-> return single element 
// div/section /article or React fragment 
//className  instead of class
//use camelCase for html attributes like onClick

// main stateless function component with multiple instances
// const title='Goodnight Moon';    variable are being removed here to change them in an object
