function BookList(){
  return( 
  <section className="booklist"> 
  {/* map function iterartes over and print values from object array correctly  */}
  {data.map((book)=>{
    return (
      <Book key = {book.id} {...book}></Book>
    );
  })}
  </section>
  ); 
}


// Rendering to HTML
ReactDom.render(<BookList />,document.getElementById('root'));